export default function validateCart(cart, totalPrice) {
    var productWithWarning = cart.filter(function (item) { return item.quantity === 10; });
    var productWithError = cart.filter(function (item) { return item.quantity > 10; });
    var resultValidate = {
        minPrice: {
            isValid: !(totalPrice < 1),
            error: '',
        },
        maxPrice: {
            isValid: !(totalPrice > 30000),
            error: 'Максимальная цена - 30 000 ₽',
        },
        warnQuantity: {
            isWarning: !productWithWarning.length,
            warning: 'Достигнут максимум - 10 шт',
            productId: productWithWarning.map(function (item) { return item.product.id; }),
        },
        maxQuantity: {
            isValid: !productWithError.length,
            error: 'Превышен максимум - 10 шт',
            productId: productWithError.map(function (item) { return item.product.id; }),
        },
    };
    return resultValidate;
}
